@import 'common/styles/bongo-vars.scss';

.bongo-loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background-color: white;

  .bongo-loader-bar {
    content: '';
    display: inline;
    position: absolute;
    width: 0;
    height: 100%;
    text-align: center;
    background-color: $bongo-color-blue;
    animation-name: loading;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
  }
}

@keyframes loading {
  from {
    left: 0;
    width: 0;
  }

  50% {
    width: 100%;
  }

  to {
    right: 0;
    width: 0;
  }
}
